    border: 1px solid #AD00FF;
}

.MessagePageContainer {
    height:100%;
    max-height:80vh;
    display: grid;
    grid-template-columns: 10% 45% 45%;
    grid-template-rows: auto;
    grid-template-areas: 'SideBar MyChat YouChat';
}
.SideBar
{
    grid-area: SideBar;
